	const form = require("../../common/utils/formValidation.js")
	import listCell from '@/components/mix-list-cell';
	import qfModal from "../../components/modal/modal";
	export default {
		data() {
			return {
				nickName: '', //微信用户名
				avatarUrl: '', //用户头像
				phone: '18452774144', //用户手机号
				newPhone: '', //新手机号
				code: '', //短信验证码
				isCanUse: false, //默认为false
				couponList: [
					{name: "未使用", imgSrc: "/static/images/user/default-img.png", type: "0"},
					{name: "已使用", imgSrc: "/static/images/user/default-img.png", type: "1"},
					{name: "已过期", imgSrc: "/static/images/user/default-img.png", type: "2"}
				],
				modal: false,
				seconds: 10,
				codeBtn: {
					text: '获取验证码',
					waitingCode: false,
					count: this.seconds
				}
			}
		},
		onLoad() {
			// this.login();
		},
		onShow() {
			this.isCanUse = uni.getStorageSync('isCanUse');
		},
		components: {
			listCell,
			qfModal
		},
		computed: {
			disableCodeBtn: function (){
				return this.codeBtn.waitingCode;
			}
		},
		methods: {
			//获取最新的昵称、头像
			upDateUserInfo(){
				
			},
			wxGetUserInfo(info) {
				let _this = this;
				const { detail } = info;
				this.nickName = detail.userInfo.nickName;
				this.avatarUrl = detail.userInfo.avatarUrl;
				try {
					uni.setStorageSync('isCanUse', true);
				} catch (e) {}
				this.isCanUse = uni.getStorageSync('isCanUse')
				//记录是否第一次授权  false:表示不是第一次授权
				// uni.getUserInfo({
				// 	provider: 'weixin',
				// 	success: function(infoRes) {
				// 		let nickName = infoRes.userInfo.nickName; //昵称
				// 		let avatarUrl = infoRes.userInfo.avatarUrl; //头像
				// 		try {
				// 			uni.setStorageSync('isCanUse', false);//记录是否第一次授权  false:表示不是第一次授权
				// 			// _this.updateUserInfo();
				// 		} catch (e) {}
				// 	},
				// 	fail(res) {}
				// });
			},
			login() {
				let _this = this;
				uni.showLoading({
					title: '登录中...'
				});
			   // 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						let code = loginRes.code;
						if (!_this.isCanUse) {
							//非第一次授权获取用户信息
							uni.getUserInfo({
								provider: 'weixin',
								success: function(infoRes) {
　　　　　　　　　　　　　　　　　　　　//获取用户信息后向调用信息更新方法
									console.log(infoRes)
									let nickName = infoRes.userInfo.nickName; //昵称
									let avatarUrl = infoRes.userInfo.avatarUrl; //头像
									// _this.updateUserInfo();//调用更新信息方法
								}
							});
						}
						uni.hideLoading();
						// //2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						// uni.request({
						// 	url: '服务器地址',
						// 	data: {
						// 		code: code,
						// 	},
						// 	method: 'GET',
						// 	header: {
						// 		'content-type': 'application/json'
						// 	},
						// 	success: (res) => {
						// 		//openId、或SessionKdy存储//隐藏loading
						// 		uni.hideLoading();
						// 	}
						// });
					},
				});
			},
			navTo(url) {
				// console.log(this.isCanUse)
				// if(!this.isCanUse){
				// 	// url = '/pages/public/login';
				// }
				uni.navigateTo({  
					url
				})
			},
			makePhoneService() {
				uni.makePhoneCall({
				    phoneNumber: this.phone
				});
			},
			openModal() {
				this.modal = !this.modal;
			},
			handleClick(e) {
				let index = e.index;
				// if (index === 0) {
				// 	this.qf.toast("你点击了取消按钮")
				// } else {
				// 	this.qf.toast("你点击了确定按钮")
				// }
				this.modal = false
			},	
			formSubmit(e) {
				let rules = [
					{
						name: 'newPhone',
						rule: ["required", "isMobile"],
						msg: ["请输入手机号", "请输入正确的手机号"]
					},
					{
						name: 'code',
						rule: ["required", "isNum", "minLength:6"],
						msg: ["请输入验证码", "请输入正确的数字", "请输入完整的验证码"]
					}
				]
				let formData = e.detail.value;
				let checkRes = form.validation(formData, rules)
				if (!checkRes) {
					uni.showToast({
						title: "验证通过!",
						icon: "none"
					});
					this.phone = formData.newPhone;
					this.newPhone = "";
					this.code = "";
					this.modal = false;
				} else {
					uni.showToast({
						title: checkRes,
						icon: "none"
					});
				}
			},
			sendCode() {
				let rules = [
					{
						name: 'newPhone',
						rule: ["required", "isMobile"],
						msg: ["请输入手机号", "请输入正确的手机号"]
					},
				]
				let checkRes = form.validation({newPhone: this.newPhone}, rules)
				if (checkRes) {
					uni.showToast({
						title: checkRes,
						icon: "none"
					});
					return;
				}
				this.codeBtn.waitingCode = true;
				this.codeBtn.count = this.seconds;
				this.codeBtn.text = this.codeBtn.count + 's';
				
				let countdown = setInterval( () => {
					this.codeBtn.count--;
					this.codeBtn.text = this.codeBtn.count + 's';
					if( this.codeBtn.count < 0 ){
						clearInterval(countdown);
						this.codeBtn.text = '重新发送';
						this.codeBtn.waitingCode = false;
					}
				},1000);
			}
		},
		created() {
			
		},
		mounted() {
			
		},
	}
